import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Button,
  Input,
  message
} from 'antd'
import {CsxSelect, CsxFormSelect, CsxValidateInput, CsxFormSearch} from 'Form'
import {API_STOCKRELOCATION_SHIPPERKEYVALUES,
  API_NEWPRODUCT_GETPRODUCTKEYVALUES_MORE,
   API_NEWPRODUCT_GETPRODUCTKEYVALUES, API_FRMLOSSDTLORDERS_GETSTORAGELOCATIONFUZZYKEYVALUES, API_CHANGE_GETPRODUCTLIST} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {GOODSCHANGECHANGETYPE} from 'Mock'

@connect(
  state => ({...state.goodsChange, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 100,
      key: 'caozuo',
      dataIndex: 'caozuo',
      render: (text, record, index) => <div className="tableOperation">
        {
          record.sourceProductCode ? <span className='red' onClick={() => {
            for (let key in record) {
              if (key == 'id') continue
              delete record[key]
            }
            this.forceUpdate()
          }}>删除</span> : null
        }
      </div>
    },
    {
      title: '原商品编码',
      width: 250,
      dataIndex: 'sourceProductCode',
      key: 'sourceProductCode',
      render: (text, record, index) => {
        const {userLoation, popData: {changeType}, getProductList} = this.props
        return (<CsxFormSearch
          // dateApi={API_NEWPRODUCT_GETPRODUCTKEYVALUES}
          dateApi={API_NEWPRODUCT_GETPRODUCTKEYVALUES_MORE}
          searchParams={{warehouseCode: userLoation.value}}
          searchParamsKey='productCode'
          selectItem={{data: record.sourceProductCode, key: 'productCode', valueKey: ['productCode', 'productName']}}
          firstData={
            !changeType || !changeType.value ? '转换类型' : null
          }
          onChangeSelect={(value) => {
            record.sourceProductCode = value
            this.forceUpdate()
          }}
          onSelect={(value, option) => {
            record.sourceProductCode = value
            record.storeLocationCode = ''
            record.availableQty = ''
            record.targetProductCode = ''
            record.proportion = ''
            record.items = []
            record.basicUnit = option && option.basicUnit || '' // 展示原料单位
            this.forceUpdate()
            getProductList({
              searchParams: {changeType: changeType && changeType.value,
                productCode: value.value
              }
            }, (data) => {
              record.items = data.items
              this.forceUpdate()
            })
          }}
        />)
      }
    },
    {
      title: '储位编号',
      width: 250,
      dataIndex: 'storeLocationCode',
      key: 'storeLocationCode',
      render: (text, record, index) => {
        const {userLoation, popData} = this.props
        return <CsxFormSearch
          dateApi={API_FRMLOSSDTLORDERS_GETSTORAGELOCATIONFUZZYKEYVALUES}
          searchParams={{
            warehouseCode: userLoation.value,
            shipperCode: popData.shipperCode && popData.shipperCode.value,
            productCode: record.sourceProductCode && record.sourceProductCode.value
          }}
          searchParamsKey='storeLocationCode'
          selectItem={{data: record.storeLocationCode, key: 'storeLocationCode', valueKey: ['storeLocationCode', 'storeLocationName']}}
          onChangeSelect={(value) => {
            record.storeLocationCode = value
            this.forceUpdate()
          }}
          firstData={
            record.sourceProductCode === undefined ? '原商品编码' : null
          }
          onFocusGetData={true}
          onFocus={(data) => {
            if (data.result.length === 0) {
              message.error(`${record.sourceProductCode.children}没有储位`)
            }
          }}
          onSelect={(value) => {
            const {handleChuweiInfo, userLoation, popData, update} = this.props
            handleChuweiInfo({
              searchParams: {
                warehouseCode: userLoation.value,
                shipperCode: popData.shipperCode.value,
                productCode: record.sourceProductCode && record.sourceProductCode.value,
                storeLocationCode: value.value
              }
            }, (data) => {
              record.unit = data[0].unit
              record.availableQty = data[0].availableQty
              this.forceUpdate()
              update({availableQty: data[0].availableQty, storeLocationCode: record.storeLocationCode})
            })
            record.storeLocationCode = value
          }}
        />
      }
    }, {
      title: '转换商品',
      width: 250,
      dataIndex: 'targetProductCode',
      key: 'targetProductCode',
      render: (text, record, index) => {
        const {userLoation, popData: {changeType}} = this.props
        return changeType && changeType.value != '3' ? <CsxFormSelect
        // antd={{ disabled: true }}
        // dateApi={API_CHANGE_GETPRODUCTLIST}
        // searchParams={{changeType: changeType && changeType.value,
        //   productCode: record.sourceProductCode && record.sourceProductCode.value
        // }}
        data={record.items || []}
        value={record.targetProductCode}
        selectItem={{key: 'productCode', valueKey: ['productCode', 'productName']}}
        handleSelectChange={(value, obj) => {
          record.targetProductCode = value
          record.proportion = obj.proportion
          record.resultUnit = obj && obj.productUnit || '' // 展示转换后的成品单位 - 子转母、母转子 使用同一个接口
          this.forceUpdate()
        }}
      /> : <CsxFormSearch
          // dateApi={API_NEWPRODUCT_GETPRODUCTKEYVALUES}
          dateApi={API_NEWPRODUCT_GETPRODUCTKEYVALUES_MORE}
          searchParams={{warehouseCode: userLoation.value}}
          searchParamsKey='productCode'
          firstData={
            !changeType || !changeType.value ? '转换类型' : null
          }
          selectItem={{data: record.targetProductCode, key: 'productCode', valueKey: ['productCode', 'productName']}}
          onChangeSelect={(value) => {
            record.targetProductCode = value
            this.forceUpdate()
          }}
          onSelect={(value, option) => {
            record.targetProductCode = value
            if (changeType && changeType.value == 3) {
              record.resultUnit = option && option.basicUnit || '' // 展示转换后的成品单位 - 等量互转  使用一个模糊搜索的商品列表
            }
            this.forceUpdate()
              // 获取转换比例
            if (changeType && changeType.value == 3) {
              record.proportion = 1
              this.forceUpdate()
            } else if (record.sourceProductCode && record.targetProductCode && changeType.value) {
              const {getChangeScale} = this.props
              getChangeScale({searchParams: {sourceProductCode: record.sourceProductCode.value, targetProductCode: record.targetProductCode.value, changeType: changeType.value}}, (resData) => {
                if (!resData.success) {
                  Modal.error({
                    title: '转换',
                    content: resData.message
                  })
                  record.sourceQty = {
                    value: '',
                    error: true
                  }
                  this.forceUpdate()
                } else {
                  record.proportion = resData.proportion
                  this.forceUpdate()
                }
              })
            }
          }}
        />
      }
    },
    {
      title: '可用数',
      width: 60,
      dataIndex: 'availableQty',
      key: 'availableQty'
    },
    {
      title: '单位',
      width: 60,
      dataIndex: 'basicUnit',
      key: 'basicUnit'
    },
    {
      title: '转换比例',
      width: 80,
      dataIndex: 'proportion',
      key: 'proportion'
    },
    {
      title: '转换数量',
      width: 120,
      key: 'sourceQty',
      render: (text, record, index) => (
          <CsxValidateInput style={{width: '100px'}} reg={(record.unit && record.unit.toUpperCase()) == 'KG' ? 'TWOO' : 'REGO'}
            value={record.sourceQty ? record.sourceQty.value == undefined ? record.sourceQty : record.sourceQty.value : ''}
            error={record.sourceQty && record.sourceQty.error || false}
            handleOnchange={(data) => {
              const {value = '', error = false} = data
              record.sourceQty = data
              const {popData: {changeType}} = this.props
              if (!error) {
                // 子品转母品 转换数量除以比例
                if (changeType && changeType.value == 1) {
                  if (Number(record.proportion)) {
                    record.resultProNum = Number(value) / Number(record.proportion)
                  } else {
                    // message.error('请核实转换比例')
                  }
                } else {
                  record.resultProNum = Number(value) * Number(record.proportion)
                }
              }
              this.forceUpdate()
            }} />)
    },

    {
      title: '成品数量',
      width: 80,
      dataIndex: 'resultProNum',
      key: 'resultProNum'
    },
    {
      title: '成品单位',
      width: 80,
      dataIndex: 'resultUnit',
      key: 'resultUnit'
    }
    ]
  }
  componentDidMount() {
    // 获取货主默认的值
    // const {handleGetSupplier, userLoation} = this.props
    // handleGetSupplier({warehouseCode:userLoation.value})
  }
  onInputChange(values, otherObj) {
    const {popData, update} = this.props
    update({popData: {...popData, ...values}, ...otherObj})
  }

  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowCreateModal: false})
  }
  // 新增报损单
  handleCommit = () => {
    const {
      handleCommit,
      createTable,
      popData,
      storeLocationCode
      // nowFrmlossQty,
    } = this.props
    if (!popData.shipperCode) {
      message.error('请选择货主')
      return
    }
    if (!popData.changeType) {
      message.error('请选择转换类型')
      return
    }
    if (popData.shipperCode.value) {
      let req = {}
      req.items = JSON.parse(JSON.stringify(createTable)).filter((o, i) => o.sourceProductCode)
      if (req.items.length == 0) {
        message.error('请输入原商品编码')
        return
      }
      if (storeLocationCode === undefined) {
        message.error('请输入储位编号')
        return
      }
      let pass = true
      let msg = ''
      for (let i = 0; i < req.items.length; i++) {
        if (req.items[i].sourceQty === undefined) {
          msg = '请输入转换数量'
          pass = false
          break
        }
        if (req.items[i].sourceQty && req.items[i].sourceQty.error === true) {
          msg = '转换数量不正确'
          pass = false
          break
        }
        if (!req.items[i].proportion) {
          msg = `原商品${req.items[i].sourceProductCode.value}转换比例不正确`
          pass = false
          break
        }
      }
      if (!pass) {
        message.error(msg)
        return
      }
      req.header = {
        shipperName: popData.shipperCode.children,
        shipperCode: popData.shipperCode.value,
        remark: popData.remark,
        changeType: popData.changeType.value
      }
      let arr = []
      req.items.map((o, i) => {
        let obj = {
          sourceProductCode: o.sourceProductCode && o.sourceProductCode.value,
          storeLocationCode: o.storeLocationCode && o.storeLocationCode.value,
          sourceQty: o.sourceQty && Number(o.sourceQty.value),
          targetProductCode: o.targetProductCode && o.targetProductCode.value
        }
        arr.push(obj)
      })
      req.items = arr
      handleCommit({data: req}, (res) => {
        message.success('操作成功')
        const {handleGetList, searchParams, page, size, update} = this.props
        console.log('searchParams is', searchParams)
        update({isShowCreateModal: false, createTable})
        handleGetList({searchParams: {...searchParams, page: 1, size}})
      })
    }
  }
  render() {
    const {
      createTableLoading,
      createTable,
      popData: {
        changeType,
        shipperCode,
        remark
      }
    } = this.props
    return (
      <Modal
        title="转换单创建"
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={1400}
      >
        <div className='content'>
          <Row type="flex" justify="start">
            <Col span={8}>
              <CsxSelect
                label='货主'
                require
                // antd={{ disabled: true }}
                dateApi={API_STOCKRELOCATION_SHIPPERKEYVALUES}
                selectItem={{data: shipperCode, key: 'key', valueKey: 'value'}}
                onChangeSelect={(value) => {
                  this.onInputChange({shipperCode: value})
                }}
              />
            </Col>
            <Col span={8}>
              <CsxSelect
                data={GOODSCHANGECHANGETYPE}
                label='转换类型'
                require
                selectItem={{data: changeType, key: 'key', valueKey: 'value'}}
                onChangeSelect={(value) => {
                  let createTableData = new Array(50).fill({})
                  createTableData.map((o, i) => {
                    createTableData[i] = {id: i}
                  })
                  this.onInputChange({changeType: value}, {createTable: createTableData})
                }}
              />
            </Col>
            <Col span={8}>
              <div className='commonInput'>
                <label>备注：</label>
                <div className='box'>
                  <Input value={remark} onChange={(e) => {
                    this.onInputChange({remark: e.target.value})
                  }} />
                </div>
              </div>
            </Col>
          </Row>
          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={record => record.id}
              loading={createTableLoading}
              columns={this.columns}
              scroll={{x: 'max-content', y: 400}}
              dataSource={createTable}
              pagination={false}
            />
            <Col>
              <p style={{color: 'red'}}>注: 称重单位（kg）可输入三位小数，非称重单位只能输入整数</p>
            </Col>
            <Row type="flex" justify="end" style={{marginTop: '15px'}}>
              <Col>
                <Button type="primary" onClick={this.handleCommit} >提交</Button>
              </Col>
            </Row>
          </div>
        </div>
      </Modal>
    )
  }
}



